/* body css */

.content {
  display: grid;
  grid-template-columns: 1fr 5fr;
  grid-template-areas: "a b";
  padding-top: 150px;
  margin-left: 4%;
  margin-right: 4%;
  min-height: 600px;
}

.content img {
  grid-area: a;
  border-radius: 20px;
  box-shadow: 4px 10px 16px rgba(0, 0, 0, 0.212);
}

.block {
  border-radius: 20px;
  min-width: 900px;
  padding: 15px;
  background-color: rgb(255, 255, 255);
  box-shadow: 21px 21px 42px #b3b3b3, -21px -21px 42px #ffffff;
}

input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.ManagerLoginBoxPAGE {
    display: flex;
    justify-content: center;

}
.ManagerLoginBoxPAGE-title {
    display: flex;
    margin-top: 90px;
}

#manager-login-text {
    margin-top: 10px;
    font-weight: 600;
    color: #444444;
    font-size: 18px;
}

.ManagerLoginBox {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "a b";
    padding: 0 20px 20px 20px;
    height: 560px;
    position: fixed;
    width: 80%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.ManagerLoginBoxBTN {
    text-decoration: none;
    outline: none;
    border-radius: 20px;
    background: #e8e8e8;
    box-shadow: 9px 9px 18px #d1d1d1, -9px -9px 18px #ffffff;
    margin: 20px;
    min-width: 200px;
    transition: 500ms ease;

    padding: 10px;
    padding-top: 50px;
    display: block;
    text-align: center;
    
}

.ManagerLoginBox .fas img{
    font-size: 45px;
    width: 89px;
    margin-bottom: 18px;
    color: #444444;
}
#manager-text{
    font-size: 18px;
    font-weight: 500;
    color: #444444;
} 

@media only screen and (max-width: 1366px) {
    .ManagerLoginBox {
